import React, { Component } from 'react'
import { Navbar, NavLink, Container, Nav } from 'react-bootstrap'
import logo from './fallout_PNG22.png'
import { Routes, Route, HashRouter as Router} from 'react-router-dom'
import {Helmet} from "react-helmet";

import Home from "../Pages/Home";
import CV from "../Pages/CV";
import Contacts from "../Pages/Contacts";

export var language = true


export default class Header extends Component {
    constructor(props){
        super(props);
        this.state = {lang : true};
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(){
        this.setState(prevState => ({
            lang : !prevState.lang
        }))
   
    }
    
    render() {
        language = this.state.lang
        return (
            <>
             <Helmet>
                <meta charSet="utf-8" />
                <title>Rostislav</title>
                <description>Personal resume website</description>
            </Helmet>
                <Navbar collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Container>
                        <Navbar.Brand href="/#/home">
                            <img
                                src={logo}
                                heigth="35"
                                width="35"
                                className="d-inline-block align-top"
                                alt="logo"
                            />Rostislav
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="mr-auto">
                                <NavLink active href="/#/cv" >{language ? "Resume" : ""}</NavLink>
                                <NavLink active href="/#/contacts" >{language ? "Contacts" : ""}</NavLink>
                                <NavLink active id="responsive-navbar-nav" href="" onClick={this.handleClick}>{language ? "" : "English"}</NavLink>
                                
                            </Nav>
                            

                        </Navbar.Collapse>
                        
                    </Container>
                </Navbar>

                <Router>
                    
                    <Routes>
                        <Route exact path="/home" element={ <Home /> } />
                        <Route exact path="/" element={ <Home /> } />
                        <Route exact path="/cv" element={ <CV/> } />
                        <Route exact path="/contacts" element={ <Contacts /> } />
                    </Routes>
                    
                </Router>

                    



            </>


        )
    }
    
}